import { Component, Element, h, Host, Prop } from '@stencil/core';
import { HostElement } from '@stencil/core/internal';

@Component({
  tag: 'soul-skeleton-text',
  styleUrl: 'soul-skeleton-text.scss',
  shadow: true,
})
export class SoulSkeletonText {
  private defaultLinesNumber = 3;
  @Prop({ reflect: true }) lines = this.defaultLinesNumber;
  @Prop({ reflect: true }) width: string;
  @Element() hostElement: HostElement;

  getLines(): number {
    return this.lines < 0 ? this.defaultLinesNumber : this.lines;
  }

  render() {
    if (this.width) {
      this.hostElement.style.setProperty('width', this.width);
    }

    const lineElements = [...Array(this.getLines())].map(() =>
      <div class="soul-skeleton-text__line"></div>
    );

    return (
      <Host>
        <div class="soul-skeleton-text">
          {lineElements}
        </div>
      </Host>
    );
  }
}
